.carousel {
    width: 100%;
    overflow-x: auto;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch; /* Enable smooth scrolling on iOS devices */
}

.carousel__container {
    display: flex;
    flex-direction: row; /* Display images horizontally */
    gap: 10px;
}

.carousel__container img {
    flex: 0 0 auto;
    width: 360px;
    height: 370px;
    border-radius: 20px;
}
